class common
{

constructor()
{
  this.wdays = ['Pon','Wto','Śro','Czw','Pią','Sob','Nie'];
}

        
capitalize(s:string): string {
          return s.charAt(0).toUpperCase() + s.slice(1);
        }

formatDate(d:Date):string
{
  let month:string = '0' + (d.getMonth() + 1), day:string = '0' + d.getDate(), year:string = '' + d.getFullYear();
  return [year, month.slice(-2), day.slice(-2)].join('-');
}

getPriceBG($price:number): string
{
    let $bc = 'danger';
    if ($price <= 80) $bc = 'success';
    else if ($price <= 150) $bc = 'info';
    else if ($price <= 300) $bc = 'warning';
    return $bc;
}
};

export default new common();




