import * as React from 'react';
import logo from './logo.svg';
import './App.css';
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom';

import Result from './components/Result';


        
class ResultList extends React.Component<Props,State>
{
    constructor(props)
    {
        super(props);
        this.state = {json: null};
    }
        
  componentDidMount() {
    fetch('/data.json')
      .then(response => response.json())
      .then(data => this.setState({ json: data }));
  } 
  
render()
{
  if (this.state.json === null)  
      return <div style={{height:'100px',textAlign:'center'}}><h1>Loading data...</h1></div>
    
  return <div>
        {this.state.json.map( (R,idx) => <Result key={idx} LOC={R.name + " " + R.country} params={R} SRC={R.SRC} DST={R.DST} year={2020} month="03" days="25-30 01-31 01-05"/>)}
  </div>
}
}

function Error(props)
{
//console.log(props);
return <h1>{props.match.params.query} - 404 NOT FOUND</h1>
}


class App extends React.Component {
        
  render() {
                          
    return (
            <Router basename={process.env.PUBLIC_URL}>
      <div className="App">
          
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Welcome to React</h2>
        </div><br/>
        
          <Route exact path={"/"} component={ResultList} />
          <Route path={"/:query"} component={Error} />

        <Link to="/KONTAKT">KONTAKT</Link>
        </div>
        </Router>          
    );
  }
}

export default App;



