import React from 'react';
import {ResultContext} from './ResultContext'

export default class NightsSelect extends React.Component {
        
  static contextType = ResultContext;
        
  render() {
      
    const {nightOpts, nightsSelect, selectedNights} = this.context;
                                    
    return (
            <div className='nights'>
            <button type='button' className='btn btn-warning noaction' title="Liczba noclegów"><img alt="" src='/static/img/nightNO.png'/></button>            
            {nightOpts.map(option => <button key={option.nights} onClick={nightsSelect} data-params={option.params} type="button" className={"btn btn-" + option.type  + (option.nights === selectedNights ? " selected" : "")}>{option.nights}</button>)}
            </div>
    );
  }
}



