import React from 'react';
import common from './common'
export const ResultContext = React.createContext(null);

export type ResultStateType = {
    selectedMonth  : string;
    selectedYear   : string;
    selectedNights : number;    
    selectedOption: string;
    nightOpts: Array<nightParams>;
};


export class optionParams {
           
           constructor(params)
           {
               let arr= params.split("|")               
               this.type      = common.getPriceBG(arr[3]);
               this.label     = arr[0].substr(8) + "-" + arr[1].substr(8);
               this.bestPrice = parseInt(arr[3], 10);
               this.dateBeg   = arr[0];
               this.dateEnd   = arr[1];
               this.prices    = params;
               this.title     = arr[3];
           }
                      
           prices:string; 
           type:string;
           title:string;
           label:string;
           bestPrice:number = 9999;
           dateBeg:string;
           dateEnd:string;
}


