 //https://reactjs.org/blog/2018/06/07/you-probably-dont-need-derived-state.html#what-about-memoization
 
 import React from 'react';
 import memoize from "memoize-one";
 import common from './common'
 import {ResultContext} from './ResultContext'
 
 
type calMark =
{
  price:string; 
  mark:string;
  ttip:string;
  days:number;
};

 
 export default class Calendar extends React.Component<Props>
{
    static contextType = ResultContext;
    
    constructor(props, context) {
    super(props);
    this.formatCell  = this.formatCell.bind(this);
    this.prepareList = this.prepareList.bind(this);
   }
   
    
    padIt(s)
    {
        return (s.length === 2) ? s : "0" + s;
    }
    
    prepareList = memoize(() =>
    {
      const {days} = this.props;
      let params = days.split(" ");

      let dBefore = params[0].split("-"), dInside = params[1].split("-"), dAfter = params[2].split("-");
      let dayTAB = [], splitTAB = [];
    
      while (dBefore[0] <= dBefore[1]) {dayTAB.push("b" + this.padIt(""+dBefore[0]));dBefore[0]++;};
      while (dInside[0] <= dInside[1]) {dayTAB.push("i" + this.padIt(""+dInside[0]));dInside[0]++;};
      while (dAfter[0]  <= dAfter[1]) {dayTAB.push("a" + this.padIt(""+dAfter[0]));dAfter[0]++;};
    
      //console.log(dayTAB);
      while(dayTAB.length)
      {
       splitTAB.push(dayTAB.slice(0,7));
       dayTAB = dayTAB.slice(7);
      }
      
      //console.log(splitTAB);
          
      return splitTAB;
    });
    
    prepareMarks = memoize((nightOpts, selectedNights) =>
    { 
        //console.log(context);
          const opts = nightOpts.filter(o => o.nights === selectedNights)[0].params;      
          let CAL_MARKS:{ [key: string]: calMark[]} = {};
             
          for (let o = 0; o < opts.length; o++)
          {
              let oTab:Array<string>  = opts[o], price:number = oTab.bestPrice;
              
              let d1 = new Date(oTab.dateBeg), m1 = d1.toLocaleString('default', { month: 'short' });
              let d2 = new Date(oTab.dateEnd), m2 = d2.toLocaleString('default', { month: 'short' });
                            
              let date1:string = ", " + d1.toLocaleString('default', { day: 'numeric' }) + " " + common.capitalize(m1);                            
              let date2:string = ", " + d2.toLocaleString('default', { day: 'numeric' }) + " " + common.capitalize(m2);
                        
              let bc:string = common.getPriceBG(price), ttip:string = price + ' zł (' + date1 + ' - ' + date2 + ')';
              

              let BEG:calMark = {};              
              BEG.price = bc;
              BEG.mark  = "begin"; 
              BEG.ttip  = ttip;
              BEG.days  = selectedNights;
              BEG.label = oTab.label;
              
              (CAL_MARKS[oTab.dateBeg] === void 0) ? CAL_MARKS[oTab.dateBeg] = Array(BEG) : CAL_MARKS[oTab.dateBeg].unshift(BEG);

              let cDate = new Date(oTab.dateBeg);
              
              if (oTab.dateBeg !== oTab.dateEnd)
              {
                cDate.setDate(cDate.getDate() + 1);
                let fDate = common.formatDate(cDate);
                while (fDate !== oTab.dateEnd)
                {
                    let CUR:calMark = {};
                    CUR.price = bc;
                    CUR.mark  = "cont"; 
                    CUR.ttip  = ttip;
                    (CAL_MARKS[fDate] === void 0) ?  CAL_MARKS[fDate] = Array(CUR) : CAL_MARKS[fDate].push(CUR);                    
                    cDate.setDate(cDate.getDate() + 1);
                    fDate = common.formatDate(cDate);
                }                
              }
                              
              let END:calMark = {};
              END.price = bc;
              END.mark  = "end"; 
              END.ttip  = ttip;
              (CAL_MARKS[oTab.dateEnd] === void 0) ?  CAL_MARKS[oTab.dateEnd] = Array(END) : CAL_MARKS[oTab.dateEnd].push(END);
              
          }
          
          //console.log(CAL_MARKS);
          return CAL_MARKS;
    });
    
    getSelectedRange = memoize( (OPT) =>
    {
        let MON = this.context.selectedMonth;
        let [BEG, END] = OPT.split("-");
        
        END = this.context.selectedYear + "-" + this.padIt(parseInt(MON,10) + +(BEG>END)) + "-" + END;
        BEG = this.context.selectedYear + "-" + MON + "-" + BEG;
        
        return [BEG, END];
    });
    
    formatCell(day, idx, CAL_MARKS, dataC)
    {           
        let MON = this.context.selectedMonth;
        if (day[0] === 'b') MON = this.padIt(parseInt(MON,10) - 1);
        if (day[0] === 'a') MON = this.padIt(parseInt(MON,10) + 1);
        const date = this.context.selectedYear + "-" + MON + "-" + day.substr(1);
        
        let CM = CAL_MARKS[date];
        
        if (CM === void 0)        
          return (<td key={idx} className={"calendar-day"+((day[0] !== "i") ? " outside" : "")}><div className="empty">{day.substr(1)}</div></td>);         

        const [BEG,END] = this.getSelectedRange(this.context.selectedOption);

        //console.log(date);        
        //console.log(RNG);
        
        //console.log(CM);

                let TIT:string = "", MRK_CL:string = '', DIV_BEG, DIV_END, selected = '';                
                for (let idx = 0; idx < CM.length; idx++)
                {
                  let MK = CM[idx];
                                    
                  if (date >= BEG && date <= END)
                  {
                      selected = " active";
                      if (date === BEG && MK.days >= 0) selected += " muted-end";
                      if (date > BEG && date < END) selected += " muted";
                      if (date === END && MK.days >= 0) selected += " muted-begin";
                  }
                  
                  if (MK.days > 0 || MK.mark !== 'end')                  
                    TIT += MK.ttip + "\n";
                  
                  MRK_CL = (MRK_CL === '') ? " alert-" + MK.price : MRK_CL;
                  
                    if (MK.mark === "begin")
                    {
                        DIV_BEG = <div className={"date begin alert-" + MK.price}>&nbsp;</div>;
                        dataC.val = MK.label;
                    }
                    if (MK.mark === "end")
                        DIV_END = <div className={"date end alert-" + MK.price}>&nbsp;</div>;                                      
                }
                
     let W:number=(100-25*+(DIV_BEG!==void 0)-25*+(DIV_END!==void 0)) + '%';
     TIT = TIT.trim();
                
       //console.log(dataC);
       return (<td key={idx} data-label={dataC.val} onClick={this.context.optionSelect} title={TIT} className={"calendar-day" + selected + ((day[0] !== "i") ? " outside" : "")}>{DIV_BEG}<div className={"date" + MRK_CL} style={{width:W}}>{day.substr(1)}</div>{DIV_END}</td>);         
    }
    
    render() 
    {               
        let dataC = {val : null};
        const dayTAB = this.prepareList();        
        const CAL_MARKS = this.prepareMarks(this.context.nightOpts, this.context.selectedNights);
        const FC = (row, idx ) => this.formatCell(row,idx,CAL_MARKS, dataC);

      return (
            <table className="calendar-table table table-condensed table-tight">
            <thead><tr className="c-weeks">{common.wdays.map((wday,idx) => <th key={idx} className="c-name">{wday}</th>)}</tr></thead>
            <tbody>
            {dayTAB.map( (row,idx) => (<tr key={idx}>{row.map(FC)}</tr>))}
            </tbody></table> 
            );
    }
} 


