import React from 'react';
import PropTypes from 'prop-types';

import Calendar from './Calendar'
import OptionSelect from './OptionSelect'
import NightsSelect from './NightsSelect'
import DepartureTimes from './DepartureTimes'

import {ResultContext, ResultStateType, optionParams} from './ResultContext'


type ResultPropsType= {
    LOC:string;
    SRC:string;
    DST:string;
};



export default class Result extends React.Component<ResultPropsType,ResultStateType> {
  
  
  parseParams(params:string):Array<optionParams>
  {
      return params.split("#").map(o => new optionParams(o));      
  }
  
constructor(props:ResultPropsType)
  {   
      super(props);
      
      for (let n=0; n < props.params.nightOpts.length; n++)
          props.params.nightOpts[n].params = this.parseParams(props.params.nightOpts[n].params);
                
//      console.log(props.params.nightOpts);
                    
      this.optionSelect = this.optionSelect.bind(this);
      this.nightsSelect = this.nightsSelect.bind(this);

      this.state = {
                  selectedMonth  : props.month,
                  selectedYear   : props.year,
                  selectedNights : props.params.selectedNights,                   
                  selectedOption : props.params.selectedOption, 
                  nightsSelect   : this.nightsSelect,
                  optionSelect   : this.optionSelect,
                  nightOpts      : props.params.nightOpts
                  };

}
    
  optionSelect(s)
  {
      let LABEL = s.target.getAttribute('data-label') || s.target.parentNode.getAttribute('data-label') || s.target.innerHTML;
      this.setState({selectedOption: LABEL});
  }
  
  nightsSelect(s)
  {
      let selectedNights = parseInt(s.target.innerHTML,10);      
      //let opts:Array<optionParams> = this.state.nightOpts.find(o => o.nights === selectedNights).params.split("#").map(o => o.split("|")).sort((l,r) => parseInt(l[3],10) - parseInt(r[3],10))[0];
      let opt:Array<optionParams> = this.state.nightOpts.find(o => o.nights === selectedNights).params.reduce((l,r) => l.bestPrice <= r.bestPrice && l.dateBeg < r.dateBeg ? l : r);
            
      this.setState({selectedNights:selectedNights, selectedOption: opt.label});
  }
  
  render()
  {
      const {SRC, DST, LOC, days} = this.props;
      
      return (
        <ResultContext.Provider value={this.state}>
        <div className="row row-eq-height bg" data-loc={LOC} data-src={SRC} data-dst={DST}>
        <div className="l-panel col-lg-7 col-md-6">
          <div data-original={"/BG/" + DST + ".jpg"} className="card h-100" style={{backgroundImage:"url(/BG/"+DST+".jpg)"}}>
            <div className="card-body"><h3 className="card-title">{LOC + " (" + DST+")"}<br/><small className="time" title="Czas lotu"><img src="/static/img/flightTime.png" alt=''/>65 - 75 minut</small></h3>
            <div className="links">
            <a className="ttip" title="Zobacz informacje na Wikitravel" target="_blank" href="https://wikitravel.org/en/Stockholm"><img src="/static/img/wiki.jpeg" alt=''/></a>
            <a className="ttip" title="Zobacz atrakcje na TripAdvisor" target="_blank" href="https://pl.tripadvisor.com/Attractions-g189852#ATTRACTION_SORT_WRAPPER"><img src="/static/img/trip.png" alt=''/></a>
            <a className="ttip" title="Zobacz lokalizację na Google Maps" target="_blank" href="https://www.google.pl/maps/place/Stockholm%2C+Sweden/@59.330135,18.06151,6z"><img src="/static/img/gmap.jpg" alt=''/></a>
            </div>
            <div className="airlines"><img className="lazy" src="/static/logos/FR.png" data-original="/static/logos/FR.png" alt="FR" title="Trasa obsługiwana przez Ryanair"/>
            <img className="lazy" src="/static/logos/W6.png" data-original="/static/logos/W6.png" alt="W6" title="Trasa obsługiwana przez Wizz Air"/>
            </div>
            </div>
          </div>
        </div>
         <div className="r-panel noselect col-lg-5 col-md-6 show-2">
              <NightsSelect/>
         
            <div className="card h-100 days">              
              <div className="nights-2 choices">            
                <div className="hdr">Wybierz termin</div>              
                <Calendar days={days}/>       
                <OptionSelect/>
                <DepartureTimes/>
              </div>
            </div>
        </div>
        
      </div>
      </ResultContext.Provider>
      );
  }

}

Result.propTypes = {
  LOC: PropTypes.string.isRequired,
  SRC: PropTypes.string.isRequired,
  DST: PropTypes.string.isRequired,
};




