import React from 'react';
import {ResultContext, optionParams} from './ResultContext'

export default class OptionSelect extends React.Component {
  static contextType = ResultContext;
  
  render() {
       const {optionSelect, selectedNights, nightOpts, selectedOption} = this.context;
      
           
      let opts:Array<optionParams> = nightOpts.find(o => o.nights === selectedNights).params;      
        return (
            <div>
            <img className="icon" alt="Możliwe terminy" title="Możliwe terminy" src="/static/img/dates.png"/>
            {opts.map( option => <span key={option.label} onClick={optionSelect} className={"option badge badge-" + option.type + (option.label === selectedOption ? " selected" : "")} title={option.title}>{option.label}</span>)}
            </div>            

    );
  }
}  
 


